body {
  margin: 0;
  padding: 20px;
  font-family: sans-serif;
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  grid-gap: 10px;
  grid-auto-rows: minmax(50px, auto);
}

#root{
  grid-column: 2/3;
  grid-row: 2;
}

h3{
  grid-column: 2/3;
  grid-row: 1;
  text-align: center;
}

*:focus {
  outline: none;
}



