:root{
  --headerInputHeight:30px;
  --appBg:#d8d6d6;
}

.App{
  text-align: center;
  border: 1px solid #bdbdbd;
  -webkit-box-shadow: 2px 2px 5px #b3b3b3;
          box-shadow: 2px 2px 5px #b3b3b3;
  background-color: var(--appBg);
}

#textInput{
  margin: 10px;
  border: none;
  width: 75%;
  height: var(--headerInputHeight);
  background-color: var(--appBg);
}

button{
  border-radius: 5px;
  border: 1px solid black;
  width: 100px;
  height: var(--headerInputHeight);
  background: none;
}

.footerText{
  text-align: right;
  margin-top: 20px;
  font-size: 1em;
  color: #3d3a3a;
}


